import { IconButton } from '@material-ui/core';
import React from 'react';

export const UserFollowBtn = ({
    children,
    isDisabled,
    click,
    classNameBtn
}) => {
    return (
        <IconButton
            className={classNameBtn}
            variant='outlined'
            disabled={isDisabled}
            onClick={click}>
            {children}
        </IconButton>
    );
};