import { Box, Paper } from '@material-ui/core';
import { Pagination } from '@material-ui/lab';
import React from 'react';
import { memo } from 'react';
import { useUsersPageStyles } from '../../styles';
import { UsersList } from './usersList/UsersList';

export const UsersContent = memo(({
    allUsersOnActivePage,
    allUsersInService,
    pageSize,
    activePage,
    isFetchingFollowButton,
    isFetchingUsers,
    setAllUsersOnActivePageT,
    setSubscribeToUserT
}) => {

    const {
        users,
        users__content,
        users__pagination,
    } = useUsersPageStyles()

    const pagesLists = Math.ceil(allUsersInService / pageSize);

    const pageReload = (event, page) => {
        return setAllUsersOnActivePageT(pageSize, page)
    }

    return (
        <section className={users}>
            <Paper className={users__content}>
                <Box className={users__pagination}>
                    <Pagination
                        count={pagesLists}
                        defaultPage={activePage}
                        showFirstButton
                        showLastButton
                        shape='rounded'
                        onChange={pageReload} />
                </Box>
                <Box>
                    <UsersList
                        allUsersOnActivePage={allUsersOnActivePage}
                        isFetchingUsers={isFetchingUsers}
                        setSubscribeToUserT={setSubscribeToUserT}
                        isFetchingFollowButton={isFetchingFollowButton} />
                </Box>
            </Paper>
        </section>
    );
});