import { LinearProgress } from '@material-ui/core';
import React, { memo } from 'react';
import { loopThroughArrayWithCallback } from '../../../../../utils/arrayFunctions/loopThroughArrayWithCallback';
import { booleanCheckCondition } from '../../../../../utils/condition/booleanCheckCondition';
import { useUsersPageStyles } from '../../../styles';
import { User } from './user/User';

export const UsersList = memo(({
    allUsersOnActivePage,
    isFetchingUsers,
    setSubscribeToUserT,
    isFetchingFollowButton
}) => {

    const { users__usersList } = useUsersPageStyles()

    const usersList = loopThroughArrayWithCallback(
        allUsersOnActivePage,
        (user) => (
            <User
                {...user}
                key={`user_${user.id}`}
                setSubscribeToUserT={setSubscribeToUserT}
                isFetchingFollowButton={isFetchingFollowButton}
            />
        )
    )

    return (
        <ul className={users__usersList}>
            {booleanCheckCondition(
                isFetchingUsers,
                <LinearProgress />,
                [...usersList]
            )}
        </ul>
    )
});