import React, { Profiler } from 'react';
import { memo } from 'react';
import DocumentTitle from 'react-document-title';
import { LocationPage } from '../../common/locationPage/LocationPage';
import { profilerOnRender } from '../../common/profiler/profilerOnRender';
import { UsersContent } from './usersContent/UsersContent';

export const UsersPage = memo((props) => {
    return (
        <>
            <DocumentTitle title='Users' />
            <LocationPage location='Users' />
            <Profiler id='users-profiler' onRender={profilerOnRender}>
                <UsersContent {...props} />
            </Profiler>
        </>
    )
});