import { Avatar, Box, Typography } from '@material-ui/core';
import React from 'react';
import { NavLink } from 'react-router-dom';
import { UserStatus } from '../../../../../common/userStatus/UserStatus';
import { useUsersPageStyles } from '../../../../styles';
import defaultProfileLogo from '../../../../../../assets/images/Default-profile-logo.png'
import { memo } from 'react';
import { SubscribeToUser } from '../../../../../common/subscribeToUser/SubscribeToUser';

export const User = memo(({
    id,
    name,
    photos = false,
    status,
    followed,
    setSubscribeToUserT,
    isFetchingFollowButton
}) => {

    const {
        user,
        user__avatar,
        avatar,
        user__info,
        user__info_$main,
        user__info_$name,
        user__info_$userDescription,
        followAndUnfollowUserButton
    } = useUsersPageStyles()

    const isDisabledSubscribeToUserBtn = isFetchingFollowButton.some(userId => userId === id)

    return (
        <li className={user}>
            <Box className={user__avatar}>
                <NavLink aria-label={`User link - ${name}`} to={`profile/${id}`}>
                    <Avatar className={avatar} src={photos.large || defaultProfileLogo} />
                </NavLink>
                <SubscribeToUser
                    userId={id}
                    isFollowed={followed}
                    isDisabled={isDisabledSubscribeToUserBtn}
                    setSubscribeToUserT={setSubscribeToUserT}
                    className={followAndUnfollowUserButton} />
            </Box>
            <Box className={user__info}>
                <Box className={user__info_$main}>
                    <NavLink aria-label={`User link - ${name}`} to={`profile/${id}`}>
                        <Typography className={user__info_$name} variant='h6'>
                            {name}
                        </Typography>
                    </NavLink>
                </Box>
                <Box className={user__info_$userDescription}>
                    <UserStatus status={status} />
                </Box>
            </Box>
        </li>
    );
});