import { makeStyles } from '@material-ui/core';

export const useUsersPageStyles = makeStyles((theme) => ({
    users: {
        paddingTop: '20px',
        [theme.breakpoints.down('sm')]: {
            paddingTop: '0',
            marginBottom: '70px'
        },
        [theme.breakpoints.down('xs')]: {
            marginBottom: '60px'
        }
    },

    users__content: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-start',
        alignItems: 'stretch'
    },

    users__pagination: {
        height: '80px',
        padding: '20px',
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        position: 'sticky',
        top: '64px',
        backgroundColor: '#ffffff',
        borderBottom: '1px solid #00000021',
        zIndex: '1000',
        [theme.breakpoints.down('sm')]: {
            padding: '15px'
        },
        [theme.breakpoints.down('xs')]: {
            top: '55px',
            height: 'auto'
        }
    },

    users__usersList: {
        padding: '20px',
        [theme.breakpoints.down('sm')]: {
            padding: '15px'
        }
    },

    user: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'flex-start',
        alignItems: 'stretch',
        marginBottom: '20px',
        minHeight: '60px',
        padding: '20px 20px 30px 20px',
        backgroundColor: '#ececec',
        borderRadius: '5px',
        '&:last-child': {
            marginBottom: '0',
        },
        [theme.breakpoints.down('sm')]: {
            padding: '15px',
            paddingBottom: '20px',
            marginBottom: '15px',

        }
    },

    user__avatar: {
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-start',
        alignItems: 'center'
    },

    followAndUnfollowUserButton: {
        marginTop: '10px'
    },

    avatar: {
        width: '100px',
        height: '100px',
        [theme.breakpoints.down('sm')]: {
            width: '80px',
            height: '80px'
        }
    },

    user__info: {
        marginLeft: '20px',
        [theme.breakpoints.down('sm')]: {
            marginLeft: '15px'
        }
    },

    user__info_$main: {
        maxWidth: '400px'
    },

    user__info_$name: {
        overflow: 'hidden',
        whiteSpace: 'nowrap',
        textOverflow: 'ellipsis'
    },

    user__info_$userDescription: {
        maxWidth: '400px'
    },

}))