import React, { memo, useCallback, useEffect } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import {
    setAllUsersOnActivePageT,
    setSubscribeToUserT
} from '../../redux/reducers/users-reducer';
import {
    getActivePage,
    getAllUsersInService,
    getAllUsersOnActivePage,
    getPageSize,
    getIsFetchingFollowButton,
    getIsFetchingUsers
} from '../../redux/selectors/users-selectors';
import { withAuthRedirect } from '../common/hoc/withAuthRedirect/withAuthRedirect';
import { UsersPage } from './usersPage/UsersPage';

const UsersContainer = (props) => {

    const {
        pageSize,
        activePage,
        setAllUsersOnActivePageT
    } = props

    const getAllUsersInActivePage = useCallback(() => {
        return setAllUsersOnActivePageT(pageSize, activePage)
    }, [setAllUsersOnActivePageT, pageSize, activePage])

    useEffect(() => {
        getAllUsersInActivePage()
    }, [getAllUsersInActivePage])

    return <UsersPage {...props} />
};

export default compose(
    connect((state) => ({
        allUsersOnActivePage: getAllUsersOnActivePage(state),
        allUsersInService: getAllUsersInService(state),
        pageSize: getPageSize(state),
        activePage: getActivePage(state),
        isFetchingFollowButton: getIsFetchingFollowButton(state),
        isFetchingUsers: getIsFetchingUsers(state)
    }), {
        setAllUsersOnActivePageT,
        setSubscribeToUserT
    }),
    memo,
    withAuthRedirect
)(UsersContainer, 'login', 'invert')