export const getUsersPage = ({ usersPage }) => {
    return usersPage
},

    getPageSize = ({ usersPage: { pageSize } }) => {
        return pageSize
    },

    getActivePage = ({ usersPage: { activePage } }) => {
        return activePage
    },

    getAllUsersOnActivePage = ({ usersPage: { allUsersOnActivePage } }) => {
        return allUsersOnActivePage
    },

    getAllUsersInService = ({ usersPage: { allUsersInService } }) => {
        return allUsersInService
    },

    getIsFetchingFollowButton = ({ usersPage: { isFetchingFollowButton } }) => {
        return isFetchingFollowButton
    },

    getIsFetchingUsers = ({ usersPage: { isFetchingUsers } }) => {
        return isFetchingUsers
    }