import { PersonAdd, RemoveCircleOutline } from '@material-ui/icons';
import React, { memo } from 'react';
import { booleanCheckCondition } from '../../../utils/condition/booleanCheckCondition';
import { UserFollowBtn } from './userFollowBtn/UserFollowBtn';

export const SubscribeToUser = memo(({
    isFollowed,
    isDisabled,
    setSubscribeToUserT,
    userId,
    className = false
}) => {

    return booleanCheckCondition(
        isFollowed,
        <UserFollowBtn
            classNameBtn={className}
            isDisabled={isDisabled}
            click={() => setSubscribeToUserT(userId, false)}
            aria-label='Unfollow' >
            <RemoveCircleOutline />
        </UserFollowBtn>,
        <UserFollowBtn
            classNameBtn={className}
            isDisabled={isDisabled}
            click={() => setSubscribeToUserT(userId, true)}
            aria-label='Follow'>
            <PersonAdd />
        </UserFollowBtn>
    )
});